<template>
  <b-col md="12" sm="12">
    <b-card no-body class="mx-4">
      <b-card-body class="p-12">
        <b-form v-on:submit.prevent="">
          <h1 tabindex="0" v-if="!form.id"><strong>Cadastrar Novo Inscrito</strong></h1>
          <h1 tabindex="0" v-if="form.id && !visualizar"><strong>Editar Inscrito</strong></h1>
          <h1 tabindex="0" v-if="visualizar"><strong>Visualizar Inscrito</strong></h1>
          <b-row>
            <b-col cols="8">
              <b-form-group tabindex="0" label="Nome Completo: *">
                <b-input-group class="mb-3">
                  <b-input-group-prepend>
                    <b-input-group-text><i class="icon-user"></i></b-input-group-text>
                  </b-input-group-prepend>
                  <b-form-input type="text" class="form-control" placeholder="Nome" autocomplete="nome"
                    v-model.trim="$v.form.nome.$model" :disabled="visualizar"  autofocus/>
                  <b-form-invalid-feedback id="input1LiveFeedback3" :state="chkState('nome')">Nome é obrigatório.</b-form-invalid-feedback>
                </b-input-group>
              </b-form-group>
            </b-col>
            <b-col sm="4">
              <b-form-group tabindex="0" label="Sexo: *">
                <b-form-radio-group v-model="$v.form.sexo.$model" :options="sexos" required :disabled="visualizar" />
              </b-form-group>
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="4">
              <b-form-group tabindex="0" label="Idade: *">
                <b-input-group class="mb-3">
                  <b-input-group-prepend>
                    <b-input-group-text>
                      <i class='fa fa-address-card'></i>
                    </b-input-group-text>
                  </b-input-group-prepend>
                  <b-form-input type="number" class="form-control" placeholder="Idade"
                    autocomplete="idade" v-model="$v.form.idade.$model" :disabled="visualizar" />
                  <b-form-invalid-feedback id="input1LiveFeedback1" :state="chkState('idade')">Idade é obrigatório.</b-form-invalid-feedback>
                </b-input-group>
              </b-form-group>
            </b-col>
            <b-col cols="4">
              <b-form-group tabindex="0" label="Data de Cadastro: ">
                <b-input-group class="mb-3">
                  <b-form-input type="date" class="form-control" placeholder="DD/MM/AAAA"  autocomplete="data"
                    v-model="$v.form.created_at.$model"
                    :disabled="visualizar" />
                  <b-form-invalid-feedback id="input1LiveFeedback1" :state="chkState('created_at')">Data de cadastro é obrigatório.
                  </b-form-invalid-feedback>
                </b-input-group>
              </b-form-group>
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="4">
              <b-form-group tabindex="0" label="Grupo: *">
                <b-form-select v-model="form.grupo_id" placeholder="Grupo para participar" class="mb-3"
                  :disabled="visualizar">
                  <option selected :value="null">Selecione</option>
                  <option v-for="item in grupos" :key="item.id" :value="item.id"> {{ item.nome }}</option>
                </b-form-select>
                <b-form-invalid-feedback id="input1LiveFeedback1" :state="chkState('grupo_id')">Grupo é obrigatório.
                </b-form-invalid-feedback>
              </b-form-group>
            </b-col>
            <b-col cols="4">
              <b-form-group tabindex="0" label="Estado: *">
                <b-form-select v-model="$v.form.estado_civil_id.$model" class="mb-3" :disabled="visualizar">
                  <option selected :value="null"> Selecione </option>
                  <option v-for="estado in estados" :value="estado.id" :key="estado.id">{{ estado.nome }} </option>
                </b-form-select>
                <b-form-invalid-feedback id="input1LiveFeedback1" :state="chkState('estado_civil_id')">Estado Civil é obrigatório.
                </b-form-invalid-feedback>
              </b-form-group>
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="6">
              <b-form-group tabindex="0" label="E-mail: *">
                <b-input-group class="mb-3">
                  <b-input-group-prepend>
                    <b-input-group-text>@</b-input-group-text>
                  </b-input-group-prepend>
                  <b-form-input type="text" class="form-control" placeholder="E-mail" autocomplete="email"
                    v-model.trim="$v.form.email.$model" :disabled="visualizar" />
                  <b-form-invalid-feedback id="input1LiveFeedback7" :state="chkState('email')">Este é um campo obrigatório, e deve ser um endereço
                    de e-mail válido.</b-form-invalid-feedback>
                </b-input-group>
              </b-form-group>
            </b-col>
            <b-col sm="4">
              <b-form-group tabindex="0" label="Celular: *">
                <b-input-group class="mb-3">
                  <b-form-input type="text" class="form-control" :disabled="visualizar" placeholder="Celular" autocomplete="nome"
                    v-mask="'(##) #####-####'" v-model="$v.form.celular.$model"  />
                  <b-form-invalid-feedback id="input1LiveFeedback3" :state="chkState('celular')">Celular é obrigatório.
                  </b-form-invalid-feedback>
                </b-input-group>
              </b-form-group>
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="8">
              <b-form-group tabindex="0" label="Nome Líder GR: *">
                <b-input-group class="mb-3">
                  <b-input-group-prepend>
                    <b-input-group-text><i class="icon-user"></i></b-input-group-text>
                  </b-input-group-prepend>
                  <b-form-input type="text" class="form-control" placeholder="Nome" autocomplete="nome"
                    v-model.trim="$v.form.nome_lider_gr.$model" :disabled="visualizar"  autofocus/>
                  <b-form-invalid-feedback id="input1LiveFeedback3" :state="chkState('nome_lider_gr')">Nome do Líder é obrigatório.</b-form-invalid-feedback>
                </b-input-group>
              </b-form-group>
            </b-col>
          </b-row>
        </b-form>
      </b-card-body>
      <b-card-footer class="p-12">
        <b-row>
          <b-col v-if="!loading && !visualizar" md="3" key="new_p">
            <b-button block variant="primary" :disabled="disable_buttons" v-on:click="onSubmit">
              <i class="fa fa-plus"></i>&nbsp;
              <span>Salvar</span>
            </b-button>
          </b-col>
          <b-col v-else-if="!visualizar" md="3">
            <b-button block variant="primary" :disabled="true" v-on:click="onSubmit">
              <b-spinner small type="grow"></b-spinner>Salvando...
            </b-button>
          </b-col>
          <b-col md="3">
            <b-button block variant="secondary" :disabled="disable_buttons" @click="voltar">
              <i class="fa fa-mail-reply-all"></i>&nbsp;&nbsp;<span>Voltar</span>
            </b-button>
          </b-col>
        </b-row>
      </b-card-footer>
    </b-card>
  </b-col>
</template>

<script>
import Vue from 'vue'
import VueSelect from 'vue-select'
import { validationMixin } from 'vuelidate'
import { required, email } from 'vuelidate/lib/validators'
import miniToastr from 'mini-toastr'
import moment from 'moment'
import Multiselect from 'vue-multiselect'

miniToastr.init()

export default {
  name: 'Editar',
  components: {
    VueSelect,
    Multiselect
  },
  mixins: [validationMixin],
  validations: {
    form: {
      idade: {
        required
      },
      nome: {
        required
      },
      nome_lider_gr: {
        required
      },
      email: {
        required,
        email
      },
      created_at: {
        required
      },
      grupo_id: {
        required
      },
      estado_civil_id: {
        required
      },
      celular: {
        required
      },
      sexo: {
        // required
      }
    }
  },
  created () {
    this.$store.dispatch('getEstadosCivis')
    this.$store.dispatch('getGrupos')
  },
  data () {
    return {
      loading: false,
      form: {
        id: '',
        nome: '',
        nome_lider_gr: '',
        email: '',
        created_at: null,
        sexo: 1,
        grupo_id: null,
        idade: null,
        estado_civil_id: null,
        celular: null
      },
      visualizar: false,
      disable_buttons: false,
      salvarForm: false,
      sexos: [
        { value: '1', text: 'Masculino' },
        { value: '2', text: 'Feminino' }
      ]
    }
  },
  computed: {
    isValid () {
      return !this.$v.form.$anyError
    },
    estados () {
      return this.$store.state.estadosCivis.listaEstadosCivi
    },
    grupos () {
      return this.$store.state.grupos.listaGrupo
    }
  },
  mounted () {
    if (this.$route.params.id !== undefined) {
      this.form.id = this.$route.params.id
      this.loadUser(this.$route.params.id)
    }

    if (this.$route.params.id !== undefined && this.$route.params.visualizar !== undefined) {
      this.visualizar = true
      this.loadUser(this.$route.params.id)
    }
  },
  methods: {
    can (permissao) {
      return this.$store.getters.validaPermissao(permissao)
    },
    loadUser (id) {
      Vue.http.get('api/workshops/' + id).then(({ body }) => {
        const { data } = body
        this.form = data
        this.form.created_at = moment(String(data.created_at)).format('YYYY-MM-DD')
      })
    },
    salvar () {
      const form = this.$v.form

      let data = {
        nome: form.nome.$model,
        email: form.email.$model,
        idade: form.idade.$model,
        created_at: form.created_at.$model,
        sexo: form.sexo.$model,
        grupo_id: form.grupo_id.$model,
        estado_civil_id: form.estado_civil_id.$model,
        celular: form.celular.$model ? form.celular.$model.replace(/[^0-9]+/g, '') : '',
        nome_lider_gr: form.nome_lider_gr.$model
      }

      this.loading = true
      if (this.form.id) {
        Vue.http.put('api/workshops/' + this.form.id, data).then(response => {
          if (response.body.status) {
            this.sucesso(response.body.message)
            let t = this
            setTimeout(function () {
              t.navigate('/usuarios')
            }, 3000)
          } else {
            this.erro(response.body.message)
          }

          this.loading = false
        }).catch(e => {
          this.loading = false
          if (e.data.data) { this.erro(e.data.data[0]) } else { this.erro('Falha ao editar usuário!') }
        })
      } else {
        Vue.http.post('api/workshops', data).then(response => {
          if (response.body.status) {
            this.sucesso(response.body.message)
            let t = this
            setTimeout(function () {
              t.navigate('/usuarios')
            }, 3000)
          } else {
            this.erro(response.body.message)
          }
          this.loading = false
        }).catch(e => {
          this.loading = false
          if (e.data.data) { this.erro(e.data.data[0]) } else { this.erro('Falha ao cadastrar usuário!') }
        })
      }
    },
    confirmar () {
      this.$bvModal.msgBoxConfirm('Tem certeza que deseja continuar?', {
        okTitle: 'Confirmar',
        cancelTitle: 'Cancelar'
      }).then(valor => {
        if (valor) {
          this.salvar()
        }
      }).catch(erro => {
        console.log(erro)
      })
    },
    onSubmit () {
      this.salvarForm = true
      if (this.validate()) {
        this.confirmar()
      }
    },
    chkState (val) {
      const field = this.$v.form[val]
      if (!field.$model && !this.salvarForm) {
        return null
      }

      return !field.$dirty || !field.$invalid
    },
    findFirstError (component = this) {
      if (component.state === false) {
        if (component.$refs.input) {
          component.$refs.input.focus()
          return true
        }
        if (component.$refs.check) {
          component.$refs.check.focus()
          return true
        }
      }
      let focused = false
      component.$children.some((child) => {
        focused = this.findFirstError(child)
        return focused
      })

      return focused
    },
    validate () {
      this.$v.$touch()
      this.$nextTick(() => this.findFirstError())
      return this.isValid
    },
    navigate (route) {
      this.$router.push(route)
    },
    voltar () {
      if (this.visualizar) { this.$router.push('/usuarios') } else {
        this.$bvModal.msgBoxConfirm('Deseja retornar para tela de pesquisa? É possível que dados informados não sejam salvos.', {
          okTitle: 'Confirmar',
          cancelTitle: 'Cancelar'
        }).then(valor => {
          if (valor) {
            this.$router.push('/usuarios')
          }
        })
      }
    },
    sucesso (mensagem) {
      this.$bvToast.toast(mensagem, {
        title: 'Sucesso',
        autoHideDelay: 10000,
        appendToast: true,
        variant: 'info'
      })
    },
    erro (mensagem) {
      this.$bvToast.toast(mensagem, {
        title: 'Erro',
        autoHideDelay: 10000,
        appendToast: true,
        variant: 'danger'
      })
    }
  }
}
</script>

<style src="vue-multiselect/dist/vue-multiselect.min.css">
</style>



