<template>
  <span>
    <b-overlay :show="overlay" rounded="sm">
    <b-card no-body>
      <b-card-header>
        <b-row>
          <b-col md="9">
            <h3 tabindex="0"><strong>Exportar PDF </strong></h3>
          </b-col>
        </b-row>
      </b-card-header>
      <b-card-body>
        <b-row>
          <b-col sm="6">
            <b-form-group tabindex="0" label="Grupo de Sala:">
              <b-form-select v-model="filtros.grupo_id" class="mb-3">
                <option selected :value="null">Selecione</option>
                <option v-for="item in grupos" :key="item.id" :value="item.id"> {{item.nome}}</option>
              </b-form-select>
            </b-form-group>
          </b-col>
          <b-col sm="4" offset-sm="1">
            <b-button block variant="dark" class="mt-4" @click="download">
              <i class="fa fa-download"></i>&nbsp;&nbsp;<span>Gerar Pdf</span>
            </b-button>
          </b-col>
        </b-row>
      </b-card-body>
    </b-card>
  </b-overlay>
  </span>
</template>

<script>
import miniToastr from 'mini-toastr'

miniToastr.init()

export default {
  created () {
    // if (!this.can('servico.index')) {
    //   this.$router.push('/home')
    // }
    this.$store.dispatch('getGrupos')
  },
  data () {
    return {
      overlay: false,
      filtros: {
        grupo_id: null
      }
    }
  },
  computed: {
    grupos () {
      return this.$store.state.grupos.listaGrupo
    }
  },
  methods: {
    can (permissao) {
      return this.$store.getters.validaPermissao(permissao)
    },
    download () {
      this.overlay = true
      window.open((process.env.ROOT_API1 + 'api/exporta/' + this.filtros.grupo_id), '_blank')
      this.overlay = false
    },
    navigate (route) {
      this.$router.push(route)
    },
    sucesso (mensagem) {
      this.$bvToast.toast(mensagem, {
        title: 'Sucesso',
        autoHideDelay: 10000,
        appendToast: true,
        variant: 'info'
      })
    },
    erro (mensagem) {
      console.log(mensagem)
      this.$bvToast.toast(mensagem, {
        title: 'Erro',
        autoHideDelay: 10000,
        appendToast: true,
        variant: 'danger'
      })
    }
  }
}
</script>

<style src="vue-multiselect/dist/vue-multiselect.min.css">

</style>
<style>
.card-text {
  font-size: 2rem;
}
</style>



